import React, { useEffect } from "react";
import { useMqttStats } from "../hooks/mqtt-hooks";
import useSound from "use-sound";

import typewriter from "../sounds/typewritersound.mp3";

function TestComp() {
  const [connectedDevices, setconnectedDevices] = useMqttStats(0);

  useEffect(() => {
    const timer = setInterval(() => {
      setconnectedDevices(connectedDevices + 1);
    }, 1000);
    return () => {
      clearInterval(timer);
    };
  }, [connectedDevices]);

  console.log("Rendered");
  return (
    <>
      <h1>Hello World - {connectedDevices}</h1>
      <BoopButton />
    </>
  );
}

const BoopButton = () => {
  const [play] = useSound(typewriter);
  return <button onClick={play}>Boop!</button>;
};

export default TestComp;
